#wrap {
    width:100%;
    #nav {
        z-index: 4;
        position: relative;
        width:100%;
        height:130px;        
        padding: 81px 0 0 0;            
        // border-top: 1px solid #eaedef;
        border-bottom: 1px solid #eaedef;
        // margin : 80px 0 0 0;
        >.container {
            width:100%;
            max-width: 1256px;
            margin: 0 auto;
            padding: 0 60px;
            .gap {
                width:100%;
                .row2 {
                    width:100%;
                    height:51px;
                    .headbox2 {
                        width:100%;
                        ul {
                            width:100%;
                            display:flex;
                            li {
                                padding:12px 10px;   
                                a {
                                    font-size: 16px;
                                    line-height: 20px;
                                    font-weight: 600;
                                    color: #2f3438;
                                    letter-spacing: -1px;
                                    &:hover {
                                        color :#00bbff;
                                    }
                                }
                            }
                        }
                    }
                    #rank-list {
                      width: 100%;
                      max-width: 191px;
                      height: 30px;
                      float: right;
                      position: absolute;
                      right: 0;
                      top: 100px;
                      left: 1265px;
                      dd {
                        width: 100%;
                          .slide {
                              width: 100%;
                              text-align: center;
                              padding: 0;
                              margin: 0;
                              height: 20px; /* 슬라이드 컨테이너의 높이에 맞게 조정 */
                              overflow: hidden;
                              .slide-item {
                                display: block;
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 100%;
                                height: 100%;
                                opacity: 0;
                                transform: translateY(-10%);
                                transition: opacity 0.5s ease, transform 0.5s ease;
                              }
                              li.active {
                                width: 100%;
                                display: block;
                                margin-right: 10px;
                                font-size: 15px;
                                letter-spacing: -0.4px;
                                transition: opacity 0.5s ease, transform 0.5s ease;
                                }
                            }   
                        }
                        .slide-item.active {
                            opacity: 1;
                            transform: translateY(0);
                          }
                        &.slide {
                          dd{
                              display: block;
                          }
                        }
                    }
                }
            }
        }
    }
}